import React, { useEffect } from "react";
import "./App.css";
import DocList from "./components/Documentation";
import axios from "axios";
import { DocumentationMembers } from "./_core";
import { makeAutoObservable } from "mobx";
import { observer } from "mobx-react";

interface Documentation {
  docs: DocumentationMembers;
  articles: {
    readme: string;
  };
}

const data = Array.from(document.head.getElementsByTagName("meta"))
  .map((q) => {
    return {
      name: q.getAttribute("name"),
      content: q.getAttribute("content"),
    };
  })
  .find((q) => {
    return q.name === "public-path";
  });

export function rootPath(path: string) {
  const data = Array.from(document.head.getElementsByTagName("meta"))
    .map((q) => {
      return {
        name: q.getAttribute("name"),
        content: q.getAttribute("content"),
      };
    })
    .find((q) => {
      return q.name === "public-path";
    });
    const iPath = `${data?.content}/` || "/"
    return `${iPath}/${path}`.replace(/\/+/, "/")
}

async function getDocumentationData(): Promise<Documentation> {
  return axios.get(rootPath(`data/data.json`)).then((q) => q.data as Documentation);
}

type Loaded<T> = { loaded: false } | { loaded: true; data: T };

const documentations = makeAutoObservable<{ data: Loaded<Documentation> }>({
  data: { loaded: false },
});

async function load() {
  if (documentations.data.loaded === true) return;
  const data = await getDocumentationData();
  documentations.data = {
    data,
    loaded: true,
  };
}

export default observer(() => {
  useEffect(() => {
    load();
  }, []);
  if (documentations.data.loaded === false) return null;

  return (
    <DocList
      documentation={documentations.data.data.docs}
      readme={documentations.data.data.articles.readme}
    ></DocList>
  );
});
